.mail-sent{
  height: 100%;
  display: flex;
  flex-direction: column;
}
.message{
  margin: auto;
  color: #fff;
  font-size: 20px;
  font-weight: 700;
  text-align: center;
}
.message__image{
  display: inline-block;
  padding-bottom: 20px;
  width: 101px;
  img{
    display: block;
    max-width: 100%;
  }
}