.change-succes{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  width: 100%;
  button{
    margin-bottom: 0;
  }
}
.change-succes__box{
  margin: auto 0;
  text-align: center;
  color: #fff;
  text-align: center;
}
.change-succes__cirle{
  border: 40px solid #4E5257;
  border-radius: 50%;
  background-color: #62656A;
  display: inline-block;
  width: 306px;
  height: 306px;
  padding: 40px;
  max-width: calc(100vw - 30px);
  max-height: calc(100vw - 30px);
  
  div{
    border-radius: 50%;
    background-color: #fff;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    img{
      width: 64px;
    }
  }
}
.change-succes__text{
  padding-top: 50px;
  font-size: 18px;
  font-weight: 500;
}