@font-face{
  font-family: AvenirNext;
  font-weight: 400;
  src: url('./assets/fonts/AvenirNext-Regular.ttf');
}
@font-face{
  font-family: AvenirNext;
  font-weight: 500;
  src: url('./assets/fonts/AvenirNext-Medium.ttf');
}
@font-face{
  font-family: AvenirNext;
  font-weight: 700;
  src: url('./assets/fonts/AvenirNext-Bold.ttf');
}
*{
  box-sizing: inherit;
}
a{
  text-decoration: none;
  
}
button{
  cursor: pointer;
}
img{
  max-width: 100%;
}

body{
  font-family: 'AvenirNext', sans-serif;
  box-sizing: border-box;
}
body,html, div{
  margin: 0;
  padding: 0;
}
.app {
  background-color: #ccc;
  background-image: url(./assets/img/app_bg.png);
  background-size: cover;
  background-position: right bottom;
  width: 100vw;
  height: 100vh;
  min-width: 320px;
  min-height: 540px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}
.app-wrap{
  margin: 0;
  padding: 0;
}
.app-start{
  background-image: none;
  background-color:#FB3030;
  background: linear-gradient(180deg, #FF7B7B, #FB3131);
}
.app_dark-bg{
  background-image: none;
  background-color: #2F3638;
  background: linear-gradient(180deg, #42454D, #2F3638);
}
.app-view{
  // outline: 1px solid #fff;
  overflow: hidden;
  width: 414px;
  height: 823px;
  max-width: 100%;
  max-height: 100%;
  padding: 15px 15px;
}
.app-box{
  // background-color: rgba(#000, 0.1);
  // outline: 1px solid #fff;
  width: 100%;
  height: 100%;
  display: flex;
  position: relative;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.app-box_padding{
  padding-top: 44px;
}
.nextView{
  border-radius: 4px;
  box-shadow: 0 0 3px 1px #fff;
  position: absolute;
  text-decoration: none;
  color: #fff;
  text-transform: uppercase;
  font-weight: bold;
  right: 50%;
  bottom: 0;
  transform: translateX(50%);
  font-size: 14px;
  display: inline-block;
  line-height: 1;
  padding: 4px;
}
.rout-linck{
  display: block;
}
.full-header{
  box-shadow: 0 3px 3px 0px rgba(#000, 0.5);
  margin: 0 -14px;
  position: absolute;
  left: -15px;
  right: -15px;
  padding-left: 15px;
  padding-right: 15px;
  top: 0;
  .app-header{
    padding: 0;
  }
}
.full-header__box{
  max-width: 100%;
  padding: 12px 0 20px 0;
  position: relative;
  
  .back-button{
    position: absolute;
    top: 0;
    left: 15px;
  }
}






