
.main{
  color: #fff;
  width: 100%;
  align-self: flex-end;
}
.user-authorization{

}

.social{

}
.social__list{
  list-style: none;
  margin: 0;
  padding: 0;
}
.social__item{
  border-radius: 6px;
  background-color: #000;
  padding: 0 15px;
  margin-bottom: 10px;
  text-align: center;
  line-height: 1;
  font-size: 15px;
  font-weight: 500;
  position: relative;
  height: 42px;
  vertical-align: middle;
  cursor: pointer;
  &::before{
    content: '';
    height: 100%;
    width: 0;
    display: inline-block;
    vertical-align: middle;
  }

  &:last-child{
    margin-bottom: 0;
  }

  &.social__item_fb{
    background-color: #29518E;
  }
  &.social__item_vk{
    background-color: #45668E;
  }
}
.social__icon{
  position: absolute;
  left: 15px;
  top: 0;
  bottom: 0;
  width: 20px;
  text-align: center;
  display: inline-block;
  &::before{
    content: '';
    display: inline-block;
    width: 0;
    height: 100%;
    vertical-align: middle;
  }
  
  img{
    display: inline-block;
    max-width: 100%;
    vertical-align: middle;
  }
}



.email{
  
}
.email__form{
  
}
.input__box{
  // outline: 1px solid #fff;
  margin-bottom: 15px;
}
.input__message{
  color: #FF5E5B;
  font-size: 14px;
  font-weight: 400;
  padding: 10px 0 0 0;
  position: relative;
  line-height: 1;
}
input::-ms-clear {
  display: none;
}
.input__box_pass{
  margin-bottom: 45px;
}
.input__message_mail{
  display: none;
}
.input__message_final{
  display: none;
}
.email__form_error{
  .input__box{
    margin-bottom: 15px;
  }
  .input__message{
    display: block;
  }
  .input__message_mail{
    span{

    }
  }
  .input__message_final{
    span{
      display: none;
    }
  }
}
.input__box_error{
  input{
    border-color: #FF5E5B;
  }
}
.input__message-icon{
  position: absolute;
  right: 0;
  bottom: 0;
}
.email__input{
  background-color: transparent;
  border-radius: 6px;
  outline: none;
  border: 1px solid #505658;;
  display: block;
  width: 100%;
  padding: 0 15px;
  height: 42px;
  text-align: left;
  line-height: 1;
  font-size: 15px;
  font-weight: 500;
  color: #fff;
  text-decoration: none;

  // margin-bottom: 15px;
  &:focus{
    border-color: #2A91A0;
  }
}
.email__input_password{
  // margin-bottom: 45px;
 
}
.email__input_button{
  background-color: #FF5E5B;
  text-align: center;
  color: #fff;
  font-size: 11px;
  font-weight: 700;
  margin-bottom: 10px;
  text-transform: uppercase;
  text-decoration: none;
}
.email__title{
  text-align: center;
  padding: 40px 0;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 14px;
  margin: 0;
}
@media (max-height: 650px){
  .email__title{
    padding: 20px 0;
  }
  .email__input_password{
    margin-bottom: 30px;
  }
}


.authorization__message{
  border-radius: 6px;
  border: 1px solid #818380;
  display: block;
  text-decoration: none;
  color: #fff;
  text-align: center;
  position: relative;
  height: 42px;
  vertical-align: middle;
  &::before{
    content: '';
    height: 100%;
    width: 0;
    display: inline-block;
    vertical-align: middle;
  }
  & > div{
    display: inline-block;
  }
  font-size: 12px;
  font-weight: 700;
}
.authorization__message-text{
  color: #fff;
}
.authorization__message-action{
  color: #25A3B4;
}