.user-test{
  align-self: flex-start;
  width: 100%;
  position: absolute;
  top: 60px;
  
}
.user-test__title{
  font-size: 20px;
  font-weight: bold;
  color: #fff;
  text-align: center;
  letter-spacing: 0.11px;
  height: 106px;
}
.user-test__yes-no{
  border-radius: 6px;
    border: 1px solid #26C7DB;
    height: 42px;
    font-size: 0;
   
  button{
    outline: none;
    border: none;
    display: inline-block;
    width: 50%;
    height: 100%;
    background-color: #343940;
    color: #fff;
    font-size: 14px;
    font-weight: 700;
    text-align: center;
    padding: 0;
    &._active{
      background-color: #26C7DB;
    }
  }
  button:first-child {
    border-radius: 6px 0 0 6px;
  }
  button:last-child {
    border-radius: 0 6px 6px 0;
  }
}
.user-test__box{
}
.user-test__options__list{
  list-style: none;
  padding: 0;
  margin: 0;
 
  li{
    background: #343940;
    border-radius: 6px;
    outline: none;
    border: 1px solid #505658;
    border-color: #26C7DB;
    display: block;
    width: 100%;
    padding: 0 15px;
    height: 42px;
    text-align: left;
    line-height: 1;
    font-size: 15px;
    font-weight: 400;
    color: #fff;
    text-decoration: none;
    position: relative;
    margin-bottom: 15px;
    position: relative;
    *{
      vertical-align: middle;
    }
    &::before{
      content: '';
      display: inline-block;
      height: 100%;
      width: 0;
      vertical-align: middle;
    }
  }
}
.user-test__options-title{
  font-size: 14px;
  font-weight: 400;
  display: block;
  padding-bottom: 4px;
  color: #898B8B;
  padding-top: 45px;
}
.user-test__add-option{
  border-radius: 6px;
  outline: none;
  border: 1px solid #505658;
  display: block;
  width: 100%;
  padding: 0 15px;
  height: 42px;
  text-align: left;
  line-height: 1;
  font-size: 12px;
  letter-spacing: 1px;
  font-weight: 500;
  color: #fff;
  text-decoration: none;
  position: relative;
  text-align: center;
  cursor: pointer;
  .-add{
    border-radius: 50%;
    border: 1px solid #fff;
    display: inline-block;
    width: 15px;
    height: 15px;
    text-align: center;
    margin-right: 10px;
    font-size: 12px;
  }
  div{
    display: inline-block;
  }
  *{
    vertical-align: middle;
  }
  &::before{
    content: '';
    display: inline-block;
    height: 100%;
    width: 0;
    vertical-align: middle;
  }
}
.user-test__options-close{
  border-radius: 50%;
  background-color: #707478;
  display: inline-block;
  width: 15px;
  height: 15px;
  text-align: center;
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%) rotateZ(45deg);
  cursor: pointer;
  font-size: 12px;
  line-height: 1.2;
}
@media (max-height:640px){
  .user-test__title{
    height: 90px;
  }
  .user-test__options-title{
    padding-top: 30px;
  }
}