.back-button{
  span{
    border: 2px solid #fff;
    border-top: 2px solid transparent;
    border-right: 2px solid transparent;
    border-radius: 3px 2px;
    width: 22px;
    height: 22px;
    transform: rotateZ(45deg);
    display: inline-block;
  }
  position: absolute;
  left: 0;
  top: 0;
  display: inline-block;
  padding: 5px;
  padding-top: 3px;
  padding-bottom: 0;
}