.enter-button{
  display: block;
  width: 100%;
  align-self: flex-end;
  button{
    border-radius: 6px;
    outline: none;
    border: none;
    display: block;
    background-color: #FF5E5B;
    text-align: center;
    color: #fff;
    font-size: 11px;
    font-weight: 700;
    text-transform: uppercase;
    text-decoration: none;
    width: 100%;
    height: 42px;
    margin: 0;
    padding: 0;
  }
}