.user-info{
  width: 100%;
}
.user-info__photo{

}
.user-info__photo-box{
  border-radius: 50%;
  background-color: #26C7DB;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  width: 100px;
  height: 100px;
  margin-left: auto;
  margin-right: auto;
  *{
    max-width: 100%;
    max-height: 100%;
  }
}
.user-info__photo-box_white{
  background-color: #fff;
}
.user-info__form{
  
}
.user-info__input{
  *{
    display: block;
    width: 100%;
  }
  input{
    background: #343940;
    border-radius: 6px;
    outline: none;
    border: 1px solid #505658;
    display: block;
    width: 100%;
    padding: 0 15px;
    height: 42px;
    text-align: left;
    line-height: 1;
    font-size: 15px;
    font-weight: 500;
    color: #fff;
    text-decoration: none;
    &:focus{
      border-color: #26C7DB;
    }
  }
  label{
    color: #ACAEB1;
    font-size: 14px;
    font-weight: 500;
    display: block;
    padding: 15px 0 10px 0;
    color: #898B8B;
  }
}
.user-gender__select{
  
}
.user-gender__box{
  border-radius: 6px;
  border: 1px solid #26C7DB;
  height: 42px;
  font-size: 0;
  button{
    outline: none;
    border: none;
    display: inline-block;
    width: 50%;
    height: 100%;
    background-color: #343940;
    color: #fff;
    font-size: 14px;
    font-weight: 700;
    text-align: center;
    padding: 0;
    &._active{
      background-color: #26C7DB;
    }
    &:first-child{
      border-radius: 6px 0 0 6px;
    }
    &:last-child{
      border-radius: 0 6px 6px 0;
    }
  }
  
}
.user-gender__label{
  color: #ACAEB1;
  font-size: 14px;
  font-weight: 500;
  display: block;
  padding: 15px 0 10px 0;
  color: #898B8B;
}
.user-info__photo-load{
  font-size: 10px;
  font-weight: 700;
  text-align: center;
  input{
    display: none;
  }
  img{
    display: inline-block;
    max-width: 100%;
  }
  span{
    display: block;
  }
}

@media (max-height:640px){
  .user-info__input label, .user-gender__label{
    padding: 6px 0 2px 0;
  }
}