.start-box{
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  .logo__image{
    border-radius: 0;
    background: transparent;
    padding: 0;
    width: 214px;
    height: 214px;
    span{
      padding-top: 5%;
    }
    img{
      height: 90%;
    }
  }
  .logo h1{
    font-size: 60px;
    margin-top: 20px;
  }
}