.logo-box{
  // outline: 1px solid #fff;
  display: flex;
  justify-content: center;
  padding: 15px 0;
  align-self: flex-start;
  width: 100%;
}
.logo{
  // outline: 1px solid #fff;
  color: #fff;
  text-align: center;
  font-size: 0;
  h1{
    margin: 0;
    font-size: 34px;
    margin-top: 4px;
    font-weight: 700;
  }
}
.logo__image{
  border-radius: 20px;
  background-color: #FB3131;
  background: linear-gradient(180deg, #FF7B7B, #FB3131);
  font-size: 0;
  width: 88px;
  height: 88px;
  padding: 15px;
  display: inline-block;
  span{
    border-radius: 50%;
    background-color: #fff;
    display: block;
    width: 100%;
    height: 100%;
    padding-top: 10px;
  }
  img{
    display: inline-block;
    max-width: 100%;
    max-height: 100%;
  }
}
@media (max-height: 650px){
  .logo-box{
    padding: 0;
  }
  .logo__image{
    width: 78px;
    height: 78px;
    padding: 10px;
  }
  .logo h1{
    font-weight: 500;
    font-size: 30px;
  }
}